.table-scrollable-container {
	&:after {
		content: "";
		display: table;
		clear: both;
	}
	table {
	}
	thead,
	tbody {
	}
	thead {
		tr {
		}
		th {
			height: 7rem;
			&:nth-child(2n + 2) {
			}
		}
	}
	tbody {
		white-space: nowrap;
		tr {
			white-space: normal;
		}
		tr + tr {
		}
		td {
			height: 7rem;
			&:nth-child(2n + 2) {
			}
		}
	}
	th,
	td {
	}
	th {
	}
	.instructions {
		width: 100%;
		text-align: center;
		display: block;
		padding: 0.25rem 1rem;
		font-size: ms(-1);
		.fa {
			margin: 0 0.25rem;
		}
	}
}
.table-scrollable {
	.parent-cell {
		font-size: ms(1);

		&:after {
		}
	}
